function sliderMain() {
    new Swiper(".main__slider .mySwiper",{
        slidesPerView: 2,
        spaceBetween: 10,
        pagination: {
            el: ".main__slider .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            360: {
                slidesPerView: 2,
            }
        }
    })
}

